import React, { useState, useEffect } from 'react';
// import { useDispatch } from 'react-redux';

import { toggleTheme } from 'src/state/ducks/ui';

import StatisticsBanner from 'src/components/StatisticsBanner';
import BestJobsTwo from './BestJobsTwo';
import HappyClients from './HappyClients';
import RegisterAs from './RegisterAs';
import HeroBannerThree from './HeroBannerThree';
import SearchByCategory from './SearchByCategory';
import ControlBusiness from './ControlBusiness';
import DownloadAppThree from './DownloadAppThree';
import RecentResumeThree from './RecentResumeThree';
import PricingPlan from './PricingPlan';
import AppBanner from 'src/components/AppBanner';
import axios from 'axios';

const HomeThree = () => {
  const token = localStorage.getItem('token');
  const [user, setUser] = useState({});

  var config2 = {
    headers: {
      'Authorization': token
    }
  }
  useEffect(() => {

    function fetchData() {
      axios.get(`https://www.togedoortestgo.site/graphql/accounts/${localStorage.id}`, config2)
        .then(res => {
          setUser(res.data)
        })
    }
    fetchData();
  }, [token]);
  console.log(user);
  console.log(token);
  return token ? (
    <>
      {/* <HeroBannerThree /> */}
      <AppBanner user={user} />
      <SearchByCategory />
      <BestJobsTwo />
      <StatisticsBanner banner3 />
      <ControlBusiness />
      <DownloadAppThree />
      <HappyClients />
      {/* <PricingPlan pricing3 /> */}
      {/* <RegisterAs /> */}
      <RecentResumeThree />
    </>
  ) : (
    <>
      <HeroBannerThree />
      <BestJobsTwo />
      <StatisticsBanner banner3 />
      <ControlBusiness />
      <DownloadAppThree />
      <HappyClients />
      {/* <PricingPlan pricing3 /> */}
      {/* <RegisterAs /> */}
      <RecentResumeThree />
    </>
  )
};

export default HomeThree;
