import React, { useEffect, useState } from 'react';
import { Button, Col, Row } from 'reactstrap';
import BestJobsCard from './BestJobsCard';
import axios from "axios";

const BestJobItems = () => {

  const [services, setServices] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(9);

  const [searchTerm, setSearchTerm] = React.useState("");
  const [searchResults, setSearchResults] = React.useState([]);





  useEffect(() => {
    async function fetchData() {
      try {

        setIsLoading(true);
        const response = await axios.get("https://www.togedoortestgo.site/graphql/services");
        setServices(response.data);
        // console.log(response.data);
        setSearchResults(response.data);
        setIsLoading(false);

      } catch (error) {
        console.log(error.response);
      }
    }
    fetchData();


  }, []);

  const handleChange = event => {
    setSearchTerm(event.target.value);
    console.log(searchTerm)
    console.log(searchResults)
  };


  React.useEffect(() => {
    const results = services.filter(service =>
      service.JobTitle
        .toLowerCase().includes(searchTerm)
    );

    console.log(results)
    setSearchResults(results);
  }, [searchTerm]);


  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = searchResults.sort((a, b) => (a.createdAt < b.createdAt) ? 1 : -1).slice(indexOfFirstPost, indexOfLastPost);
  const paginate = (pageNumber) => setCurrentPage(pageNumber);





  return (
    <>
      <Col  >
        <input placeholder="Search project name "
          value={searchTerm}
          onChange={handleChange}
          className="search-bar col-lg-6"
          type="text" />
      </Col>
      <Row>
        {currentPosts &&
          currentPosts.map((item, i) => (
            <Col md={12} lg={6} key={i}>
              <BestJobsCard {...item} />
              {/* <h5>{item.name}</h5> */}
            </Col>
          ))}
        <Col lg={12} className="text-center mt-4 mt-md-7">
          <Button className="rounded-pill gradient-button border-0">
            View all
          </Button>
        </Col>
      </Row>
    </>
  );
};


